import React, { useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import {
  Button,
  Checkbox,
  FormControlLabel,
  TextField,
} from "@material-ui/core";
import { baseURL } from "../services/api";
import axios from "axios";
import { toast } from "react-toastify";

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50;
  const left = 50;

  return {
    top: `${top}%`,
    left: `${left}%`,
    transform: `translate(-${top}%, -${left}%)`,
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: 400,
    backgroundColor: theme.palette.background.paper,
    border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

//@ts-ignore
export default function AddScaleModal({ modalVisible, setModalVisible }) {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = useState(getModalStyle);
  const [scaleName, setScaleName] = useState("");

  async function saveScale(e: any) {
    e.preventDefault();
    try {
      const res = await axios.post(baseURL + "/scale", {
        name: scaleName,
        status: "offline",
      });
      console.log(`res.data`, res.data);
      toast.success("Balanza creada con exito");
      setModalVisible(false);
    } catch (error) {
      console.log(`error saveScale`, error);
    }
  }

  const body = (
    <div style={modalStyle} className={classes.paper}>
      <h2 id="simple-modal-title">Anãdir Balanza</h2>
      <form className={classes.form} noValidate>
        <TextField
          variant="outlined"
          margin="normal"
          required
          fullWidth
          label="Nombre de la balanza"
          autoFocus
          value={scaleName}
          onChange={(e) => setScaleName(e.target.value)}
        />
        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={(e) => saveScale(e)}
        >
          Confirmar
        </Button>
      </form>
    </div>
  );

  return (
    // <div>
    //   <button type="button" onClick={handleOpen}>
    //     Open Modal
    //   </button>
    <Modal open={modalVisible} onClose={setModalVisible}>
      {body}
    </Modal>
    // </div>
  );
}
