import {
  Button,
  Card,
  CardActions,
  CardContent,
  makeStyles,
  Typography,
} from "@material-ui/core";
import axios from "axios";
import React, { useContext, useEffect, useState } from "react";
import { toast } from "react-toastify";
import ScaleIcon from "../assets/icons/scale";
import { AuthContext } from "../context/Auth";
import { baseURL } from "../services/api";
import { theme } from "../theme";

// import { Container } from './styles';

const useStyles = makeStyles((theme) => ({
  card: {
    height: "100%",
    display: "flex",
    flexDirection: "column",
  },
  cardContent: {
    flexGrow: 1,
  },
}));

function CardScale({ balanza, graphData }) {
  const classes = useStyles();
  const { userName } = useContext(AuthContext);

  let scale = balanza;
  // scale.userName = graphData.givenName;

  graphData?.givenName
    ? (scale.userName = graphData.givenName)
    : (scale.userName = userName);

  async function saveWeightInApiServer() {
    // console.log(`scale`, scale);
    scale.scaleName = scale.name;
    delete scale.name;
    try {
      let { data } = await axios.post(baseURL + "/registry", scale);
      // console.log("====================================");
      // console.log("Peso salvo!!!", data);
      // console.log("====================================");
      toast.success(data.message);
    } catch (error) {
      // console.log(err);
      // console.log("====================================");
      toast.error(error.response.data.errorMessage);
    }
  }

  async function deleteBalanza() {
    if (balanza.status == "online") {
      toast.error("No es posible eliminar una balanza online");
      return;
    }
    try {
      let { data } = await axios.delete(baseURL + `/scale/${balanza.id}`);
      toast.success(data.message);
      // console.log(`data`, data);
    } catch (error) {
      // console.log(`error deleteBalanza`, error);
      toast.error(error.response.data.errorMessage);
    }
  }

  return (
    <Card className={classes.card}>
      <ScaleIcon
        width={100}
        height={100}
        color={balanza.status === "online" ? "green" : "red"}
      />
      <CardContent className={classes.cardContent}>
        <Typography>{balanza.name}</Typography>
        <Typography gutterBottom variant="h5" component="h2">
          {balanza.weight ? balanza.weight : "0,00 KG"}
        </Typography>
      </CardContent>
      <CardActions>
        {balanza.status === "online" ? (
          <Button
            onClick={saveWeightInApiServer}
            fullWidth
            size="small"
            color="primary"
          >
            Capturar Peso
          </Button>
        ) : null}

        <Button
          onClick={deleteBalanza}
          fullWidth
          size="small"
          color="secondary"
        >
          Borrar Balanza
        </Button>
      </CardActions>
    </Card>
  );
}

export default CardScale;
