import React, { useContext, useEffect, useState } from "react";
import TextStyled from "../../components/TextStyled";

import Icon, {
  AntDesign,
  MaterialCommunityIcons,
  // @ts-ignore
} from "react-web-vector-icons";
//@ts-ignore
import { AiFillEyeInvisible } from "@react-icons/all-files/ai/AiFillEyeInvisible";
import { AiFillEye } from "@react-icons/all-files/ai/AiFillEye";

import azureADLogo from "../../assets/icons/logo-ad-connect.png";

import {
  Container,
  FormContainer,
  InnerFormContainer,
  CustomInput,
  LoginButton,
  PasswordInputContainer,
  EyeButton,
  LogoContainer,
  LoginButtonAzureAD,
} from "./styles";
import { theme } from "../../theme";
import ScaleIcon from "../../assets/icons/scale";
import { useMsal } from "@azure/msal-react";
import axios from "axios";
import { baseURL } from "../../services/api";
import { AuthContext } from "../../context/Auth";
import { toast } from "react-toastify";

function LoginPage() {
  const [passwordVisible, setPasswordVisible] = useState(false);
  const { instance } = useMsal();
  const [email, setEmail] = useState("italo");
  const [password, setPassword] = useState("teste123");
  //@ts-ignore
  const { setUserName, setIsSigned } = useContext(AuthContext);

  async function login() {
    try {
      let { data } = await axios.post(baseURL + "/user/login", {
        email,
        password,
      });
      console.log(`data`, data);
      setIsSigned(true);
      setUserName(data.userName);
      localStorage.setItem("@sub/logintype", "api");
      localStorage.setItem("@sub/userName", data.userName);
    } catch (error) {
      console.log(`error`, error);
      toast.error(error.response.data.errorMessage);
    }
  }

  return (
    <Container>
      <FormContainer>
        <TextStyled title center>
          Sistema Unificado de Balanzas
        </TextStyled>
        <LogoContainer>
          <ScaleIcon width={100} height={100} color={theme.primary} />
          {/* <MdLinearScale
            // name="scale"
            size={100}
            color={theme.primary}
          /> */}
          {/* <FaBeer /> */}
        </LogoContainer>
        <InnerFormContainer>
          <TextStyled>Usuario</TextStyled>
          <CustomInput
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
        </InnerFormContainer>
        <InnerFormContainer>
          <TextStyled>Contraseña</TextStyled>
          <PasswordInputContainer>
            <CustomInput
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              type={passwordVisible ? "text" : "password"}
            />
            <EyeButton onClick={() => setPasswordVisible(!passwordVisible)}>
              {passwordVisible ? (
                <AiFillEye size={24} />
              ) : (
                <AiFillEyeInvisible size={24} />
              )}
            </EyeButton>
          </PasswordInputContainer>
        </InnerFormContainer>
        <InnerFormContainer>
          <LoginButton onClick={login}>
            <TextStyled large center color="#fff">
              Login
            </TextStyled>
          </LoginButton>
        </InnerFormContainer>
        <InnerFormContainer>
          <TextStyled>----------- O ------------</TextStyled>
        </InnerFormContainer>
        <InnerFormContainer>
          <LoginButtonAzureAD
            onClick={() => {
              instance.loginRedirect().catch((e) => {
                console.log(e);
              });
            }}
          >
            <img style={{ height: 40, width: 40 }} src={azureADLogo} />
            <TextStyled large center color={theme.primary}>
              Login con Azure AD
            </TextStyled>
            <div style={{ height: 40, width: 40 }} />
          </LoginButtonAzureAD>
        </InnerFormContainer>
      </FormContainer>
    </Container>
  );
}

export default LoginPage;
