import React, { useContext, useEffect, useState } from "react";
import {
  AuthenticatedTemplate,
  UnauthenticatedTemplate,
  useMsal,
} from "@azure/msal-react";
import { loginRequest } from "./authConfig";
import { PageLayout } from "./components/PageLayout";
import { ProfileData } from "./components/ProfileData";
import { callMsGraph } from "./graph";
import Button from "react-bootstrap/Button";
import "./styles/App.css";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import LoginPage from "./screens/LoginPage/index";
import Loader from "react-loader-spinner";
import { Home } from "./screens/HomePage";
import AuthProvider, { AuthContext } from "./context/Auth";
import { ToastContainer, toast } from "react-toastify";

import "react-toastify/dist/ReactToastify.css";

/**
 * Renders information about the signed-in user or a button to retrieve data about the user
 */
const ProfileContent = () => {
  const { instance, accounts } = useMsal();
  const [graphData, setGraphData] = useState(null);

  function RequestProfileData() {
    // Silently acquires an access token which is then attached to a request for MS Graph data
    instance
      .acquireTokenSilent({
        ...loginRequest,
        account: accounts[0],
      })
      .then((response) => {
        callMsGraph(response.accessToken).then((response) => {
          setGraphData(response);
          localStorage.setItem("@sub/logintype", "azure");
        });
      });
  }

  useEffect(() => {
    RequestProfileData();
  }, []);

  return (
    <>
      {/* <h5 className="card-title">Bienvenido(a) {accounts[0].name}</h5> */}
      {graphData ? (
        <>
          <Home graphData={graphData} />
          {/* <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={10000} //3 secs
          /> */}
        </>
      ) : (
        // <Button variant="secondary" onClick={RequestProfileData}>
        //   Verificar informaciones del usuario
        // </Button>
        <div
          style={{
            display: "flex",
            flex: 1,
            justifyContent: "center",
            alignItems: "center",
          }}
        >
          <Loader
            type="Puff"
            color="#00BFFF"
            height={100}
            width={100}
            timeout={3000} //3 secs
          />
        </div>
      )}
      {/* <ProfileData graphData={graphData} /> */}
    </>
  );
};

/**
 * If a user is authenticated the ProfileContent component above is rendered. Otherwise a message indicating a user is not authenticated is rendered.
 */
const MainContent = () => {
  const { isSigned } = useContext(AuthContext);
  return (
    <div className="App">
      <AuthenticatedTemplate>
        <ProfileContent />
      </AuthenticatedTemplate>

      <UnauthenticatedTemplate>
        {isSigned ? <Home /> : <LoginPage />}
      </UnauthenticatedTemplate>
    </div>
  );
};

export default function App() {
  return (
    <>
      <ToastContainer />
      <AuthProvider>
        <PageLayout>
          <MainContent />
        </PageLayout>
      </AuthProvider>
    </>
  );
}
